.callout .chart-wrapper {
  position: absolute;
  top: 10px;
  left: 50%;
  float: right;
  width: 50%;
}

.callout-bordered {
  border: 1px solid #c8ced3;
  border-left-width: 4px;
}

.callout code {
  border-radius: 0.25rem;
}

.callout h4 {
  margin-top: 0;
  margin-bottom: .25rem;
}

.callout p:last-child {
  margin-bottom: 0;
}

.callout + .callout {
  margin-top: -0.25rem;
}

